import React from "react";
import "./EmailInstructions.css";
import { isMobile } from "..";
import mySvg from "../../images/suggestions.svg";

export default class EmailInstructions extends React.Component {
  getEmojie = (emojie = "") => {
    return (
      <span role="img" aria-label="arrow" className="padding-right-2">
        {emojie}
      </span>
    );
  };

  render() {
    const isMobVal = isMobile();

    return (
      <div
        className={`${isMobVal ? "instruction-img-mob" : "instruction-img"}`}
      >
        <div
          className={`${
            isMobVal ? "background-image-mob" : "background-image"
          }`}
          style={{
            backgroundImage: `url(${mySvg})`,
            backgroundRepeat: "no-repeat",
            backgroundSize: `${isMobVal ? "156px 212px" : "317px 212px"}`,
            WebkitBackgroundSize: `${isMobVal ? "156px" : "317px 212px"}`,
            MozBackgroundSize: `${isMobVal ? "156px" : "317px 212px"}`,
            OBackgroundSize: `${isMobVal ? "156px" : "317px 212px"}`,
            textAlign: "center",
            position: "relative",
            height: `${isMobVal ? "200px" : "212px"}`,
            width: `${isMobVal ? "150px" : "200px"}`,
          }}
        ></div>
        <div
          className={`container font-style-lobster ${
            isMobVal ? "container_mobile" : ""
          }`}
        >
          <ol className={`${isMobVal ? "ol_mobile" : "ol"}`}>
            <li>It works only for Gmail Users (Free / Premium).</li>
            <li>
              Gmail Sharable Link works only if another person there in Mail
              thread.
            </li>
            <li>This page will not store any of your data.</li>
          </ol>
        </div>
      </div>
    );
  }
}
